import React from 'react'
import Grid from '@mui/material/Grid';
import Checkbox from '@mui/material/Checkbox';
import FormControl from '@mui/material/FormControl';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormGroup from '@mui/material/FormGroup';
import FormLabel from '@mui/material/FormLabel';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import {useFormik} from "formik";
import {loginTC} from "../../State/auth-reducer";
import {useDispatch, useSelector} from 'react-redux';
import { AppDispatch, AppRootStateType} from "../../State/store";
import { Navigate } from 'react-router-dom';


type FormikErrorType = {
    email?: string
    password?: string
    rememberMe?: boolean
}
export const Login = () => {
    const dispatch = useDispatch<AppDispatch>();
    const isLoggedIn = useSelector<AppRootStateType>(state => state.auth.isLoggedIn);

    const formik = useFormik({
        initialValues: {
            email: '',
            password: '',
            rememberMe: false
        },
        validate: (values) => {
            const errors: FormikErrorType = {};
            if(values.password.length <= 2) {
                errors.password='Password must be >2 symbol!';
            }
            if (!values.email) {
                errors.email = 'Required';
            } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
                errors.email = 'Invalid email address';
            }
            return errors;
        },
        onSubmit: values => {
            formik.resetForm();
            dispatch(loginTC(formik.values.email, formik.values.password, formik.values.rememberMe))
            alert(JSON.stringify(values));
        },
    })
    console.log("formik: ", formik.touched.email);

    return isLoggedIn === true  ? <Navigate to={'/'}/>
        : <Grid container justifyContent={'center'}>
        <Grid item justifyContent={'center'}>
            <form onSubmit={formik.handleSubmit}>
            <FormControl>
                <FormLabel>
                    <p>To log in get registered
                        <a href={'https://social-network.samuraijs.com/'}
                           target={'_blank'}> here
                        </a>
                    </p>
                    <p>or use common test account credentials:</p>
                    <p>Email: free@samuraijs.com</p>
                    <p>Password: free</p>
                </FormLabel>
                <FormGroup>
                    <TextField label="Email" margin="normal"
                        {...formik.getFieldProps('email')}
                               /*onChange={(e)=>{
                        formik.touched.email = undefined;
                        formik.handleChange(e)}}*/
                               onBlur={formik.handleBlur}
                    />
                    {
                        formik.touched.email && formik.errors.email ? <div style={{color: "red"}}>{formik.errors.email} </div> : null}
                    <TextField type="password" label="Password"
                               margin="normal"
                               {...formik.getFieldProps('password')}
                        /*name={'password'}
                               onChange={formik.handleChange} value={formik.values.password}*/
                               onBlur={formik.handleBlur}
                    />
                    {formik.errors.password && <div style={{color: "red"}}>{formik.errors.password} </div>}
                    <FormControlLabel label={'Remember me'} control={<Checkbox onChange={formik.handleChange}
                                                                               checked={formik.values.rememberMe}
                                                                               name={'rememberMe'}
                    />}/>
                    <Button type={'submit'} variant={'contained'} color={'primary'}>
                        Login
                    </Button>
                </FormGroup>
            </FormControl>
            </form>
        </Grid>
    </Grid>
}

